@use "../../styles/partials/global";

.sidebar-container {
  margin: 104px 0 0 0;
  width: 0;
  height: 100vh;
  background: linear-gradient(
      217deg,
      rgba(10, 162, 231, 0),
      rgba(10, 162, 231, 0) 70.71%
    ),
    linear-gradient(336deg, rgba(10, 162, 231, 0), rgba(76, 243, 198, 0) 90.71%);
  position: fixed;
  top: 0;
  transition: 0.3s;
  display: none;
  box-shadow: 3px 4px 8px 0px rgba(0, 0, 0, 0.8);
  border-radius: 4px;
  @include global.tablet {
    margin: 104px 0 0 10px;
    height: 90vh;
  }
  @include global.desktop {
    width: 100%;
    height: 65px;
    top: 0;
    transition: 0.3s;
    background-color: transparent;
    margin: 0;
    display: flex;
    justify-content: flex-end;
    box-shadow: none;
  }

  &--active {
    @include global.mobile {
      margin: 104px 0 0 0;
      position: fixed;
      top: 0;
      z-index: 3;
      width: 100%;
      background: linear-gradient(
          217deg,
          rgba(10, 162, 231, 1),
          rgba(10, 162, 231, 0) 70.71%
        ),
        linear-gradient(
          336deg,
          rgba(76, 243, 198, 1),
          rgba(76, 243, 198, 0.4) 90.71%
        );
      transition: left 0.3s ease-out, right 0.3s ease-out;
      opacity: 0.97;
    }
    @include global.tablet {
      position: fixed;
      top: 0;
      z-index: 3;
      width: 385px;
      background: linear-gradient(
          217deg,
          rgba(76, 243, 198, 0.8),
          rgba(10, 162, 231, 0) 70.71%
        ),
        linear-gradient(
          336deg,
          rgba(10, 162, 231, 1),
          rgba(76, 243, 198, 0.4) 90.71%
        );
      transition: left 0.3s ease-out, right 0.3s ease-out;
      margin: 104px 0 0 10px;
      border-radius: 4px;
    }
    @include global.desktop {
      transition: 0.3s;
      width: 100%;
      background: linear-gradient(
          217deg,
          rgba(10, 162, 231, 1),
          rgba(10, 162, 231, 0) 70.71%
        ),
        linear-gradient(
          336deg,
          rgba(10, 162, 231, 1),
          rgba(76, 243, 198, 0.4) 90.71%
        );
      margin: 0;
    }
  }

  &__sidebar-width {
    @include global.desktop {
      display: flex;
      align-items: center;
      justify-content: center;
      max-width: 1300px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    padding: 0;
    height: 100vh;
    margin: 0;
    box-shadow: 3px 4px 8px 0px rgba(0, 0, 0, 0.8);
    &-link {
      cursor: pointer;
    }
    @include global.tablet {
      height: 87.5vh;
    }

    @include global.desktop {
      flex-direction: row;
      justify-content: flex-end;
      height: 95px;
      box-shadow: none;
    }
    &-item {
      position: relative;
      margin: 0 0 50px 0;
      @include global.desktop {
        margin: 0 20px 0 0;
      }
    }
    &-home,
    &-about,
    &-skills,
    &-work,
    &-contact {
      width: 40px;
      height: 40px;
      @include global.desktop {
        width: 20px;
        height: 20px;
        margin: 0 10px 0 0;
        display: none;
      }
    }
  }
  &__list-link {
    display: flex;
    align-items: flex-end;
    width: 220px;
    justify-content: flex-start;
    filter: drop-shadow(0.4rem 0.3rem 0.1rem rgba(86, 255, 255, 0.8));
    transition: filter 600ms;
    &:hover {
      filter: drop-shadow(0.2rem 0.3rem 0.1rem rgba(174, 214, 241, 0.8));
    }
    @include global.desktop {
      filter: none;
      display: flex;
      text-decoration: none;
      width: auto;
      justify-content: flex-end;
    }
  }
  &__hover-home,
  &__hover-about,
  &__hover-skills,
  &__hover-projects,
  &__hover-contact {
    display: none;
    font-family: "RalewaySB";
    color: #444649;
    display: flex;
    font-size: 30px;
    font-weight: bold;
    padding: 0 0 0 30px;
    margin: 0;
    text-shadow: none;
    &:hover {
      text-shadow: none;
    }

    @include global.desktop {
      font-family: "RalewayB";
      color: #444649;
      display: flex;
      margin: 0;
      align-self: flex-end;
      font-size: 20px;
      font-weight: bold;
      &:hover {
        color: #ffffff;
      }
    }
  }

  &__backdrop {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 95px;
    left: 0;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.6);
    transition: opacity 0.3s ease-out, visibility 0.3s ease-out;
    @include global.desktop {
      display: none;
    }
  }
  &__backdrop-false {
    display: none;
  }
  &__social-container {
    width: 100px;
    @include global.desktop {
      display: none;
    }
  }
  &__linkedin-image {
    width: 40px;
    height: 40px;
    &:hover {
      cursor: pointer;
    }
  }
  &__github-image {
    width: 35px;
    height: 35px;
    &:hover {
      cursor: pointer;
    }
  }
  &__top-hr {
    border-radius: 11px;
    border: 1px solid #444649;
  }
  &__linkedin-image {
    margin: 0 10px 0 0;
    filter: drop-shadow(0.4rem 0.3rem 0.1rem rgba(86, 255, 255, 0.8));
    transition: filter 600ms;
    &:hover {
      filter: drop-shadow(0.2rem 0.3rem 0.1rem rgba(174, 214, 241, 0.8));
    }
  }
  &__github-image {
    margin: 0 0 0 10px;
    filter: drop-shadow(0.4rem 0.3rem 0.1rem rgba(86, 255, 255, 0.8));
    transition: filter 600ms;
    &:hover {
      filter: drop-shadow(0.2rem 0.3rem 0.1rem rgba(174, 214, 241, 0.8));
    }
  }
  &__flex-list {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include global.desktop {
      display: flex;
      flex-direction: row;
    }
  }
}
